.App {
  /*background-color: #052427;*/
}

.App-header {
  background-color: #14435B;
  background-image: url("./lizard.jpg");
  background-position: right;
  background-repeat: no-repeat;
  height: 129px;
  padding: 10px;
  color: #FFE056;
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.App-title {
  text-align: center;
  font-size: 3em;
}

.App-intro {
  font-size: large;
}

.webcam {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.webcam-controls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
}

.image-result {
  margin-top: 40px;
}



